methods: {
...mapActions([]),
showDeleteButton(e) {
e.preventDefault();
e.stopPropagation();
let that = this;
if(document.querySelectorAll('.list-iconbox')[0].classList.contains('active')){
var iconboxes = document.querySelectorAll('.list-iconbox');
iconboxes.forEach(e => {
e.classList.remove('active');
})
that.isclick = true
}else{
that.timeOutEvent = setTimeout(function(){
var iconboxes = document.querySelectorAll('.list-iconbox');
iconboxes.forEach(e => {
e.classList.add('active');
})
},500)
}
},
cancelShow(index,url,isMine) {
clearTimeout(this.timeOutEvent);
if(this.isclick) {
if(isMine) {
console.log(this.listmine[index]);
}else {
console.log(this.listshop[this.selectindex].functionLists[index]);
}
}else if(!document.querySelectorAll('.list-iconbox')[0].classList.contains('active')){
window.location.href = url; 
}
this.isclick = false;
},
editIcon() {
this.listshop.forEach(e => {
e.functionLists.forEach(ele => {
this.listmine.forEach(element => {
if(element.title === ele.title) {
ele.operationicon = false;
}
})
})
})
},
hideBox() { //点击非icon区域，隐藏操作按钮
var iconboxes = document.querySelectorAll('.list-iconbox');
iconboxes.forEach(e => {
